"use client";

import { useState } from "react";
import { useParams } from "next/navigation";

export default function BusinessPage() {
  const { id } = useParams();

  // Fixed transport costs
  const TRANSPORT_COST = { Idu: 40000, Aviation: 80000 };

  // Fixed product prices per ton
  const PRODUCT_PRICES = { Dust: 12000, "3/8": 12000, "3/4": 11000 };

  // Mock daily logs
  const [logs, setLogs] = useState([
    {
      id: 1,
      date: "2025-10-24",
      location: "Idu",
      products: [
        { name: "Dust", tonnagePerTrip: [26.36, 27.28] },
        { name: "3/8", tonnagePerTrip: [10, 12] },
      ],
      trips: 2,
      paymentStatus: "Paid",
    },
    {
      id: 2,
      date: "2025-10-23",
      location: "Aviation",
      products: [{ name: "Dust", tonnagePerTrip: [15, 20] }],
      trips: 2,
      paymentStatus: "Pending",
    },
  ]);

  const [showModal, setShowModal] = useState(false);
  const [newLog, setNewLog] = useState({
    date: "",
    location: "Idu",
    trips: 1,
    products: [{ name: "Dust", tonnagePerTrip: [0] }],
    paymentStatus: "Pending",
  });

  const calculateProductTotal = (product) =>
    product.tonnagePerTrip.reduce(
      (sum, tons) => sum + tons * PRODUCT_PRICES[product.name],
      0
    );

  const calculateTransportTotal = (log) =>
    log.trips * TRANSPORT_COST[log.location];

  const calculateGrandTotal = (log) =>
    log.products.reduce((sum, p) => sum + calculateProductTotal(p), 0) +
    calculateTransportTotal(log);

  const handleAddLog = () => {
    const nextId = logs.length ? Math.max(...logs.map((l) => l.id)) + 1 : 1;
    setLogs([...logs, { id: nextId, ...newLog }]);
    setNewLog({
      date: "",
      location: "Idu",
      trips: 1,
      products: [{ name: "Dust", tonnagePerTrip: [0] }],
      paymentStatus: "Pending",
    });
    setShowModal(false);
  };

  const formatCurrency = (amount) =>
    new Intl.NumberFormat("en-NG", {
      style: "currency",
      currency: "NGN",
    }).format(amount);

  const formatDate = (dateString) =>
    new Date(dateString).toLocaleDateString("en-US", {
      weekday: "short",
      year: "numeric",
      month: "short",
      day: "numeric",
    });

  // Filter logs by month
  const [selectedMonth, setSelectedMonth] = useState(new Date().getMonth() + 1); // 1-12
  const filteredLogs = logs.filter(
    (log) => new Date(log.date).getMonth() + 1 === selectedMonth
  );

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 p-6">
      {/* Header */}
      <div className="max-w-7xl mx-auto mb-8">
        <div className="flex justify-between items-start">
          <div>
            <div className="flex items-center gap-3 mb-2">
              <button
                onClick={() => window.history.back()}
                className="p-2 hover:bg-white/50 rounded-2xl transition-colors duration-200"
              >
                <svg
                  className="w-5 h-5 text-slate-600"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M15 19l-7-7 7-7"
                  />
                </svg>
              </button>
              <h1 className="text-3xl font-bold text-slate-900">
                Business #{id}
              </h1>
            </div>
            <p className="text-slate-600 text-lg ml-12">Daily Operations Log</p>
          </div>

          {/* Stats Overview */}
          <div className="flex items-center gap-6">
            <div className="text-right">
              <p className="text-sm text-slate-500">Total Revenue</p>
              <p className="text-xl font-bold text-slate-900">
                {formatCurrency(
                  filteredLogs.reduce(
                    (sum, log) => sum + calculateGrandTotal(log),
                    0
                  )
                )}
              </p>
            </div>
            <button
              onClick={() => setShowModal(true)}
              className="bg-gradient-to-r from-emerald-600 to-green-600 text-white px-6 py-3 rounded-2xl hover:from-emerald-700 hover:to-green-700 shadow-lg hover:shadow-xl transition-all duration-300 flex items-center gap-2"
            >
              New Log Entry
            </button>
          </div>
        </div>

        {/* Month Filter */}
        <div className="mt-4 flex gap-3">
          <label className="text-sm font-medium text-slate-700">
            Filter by Month:
          </label>
          <select
            value={selectedMonth}
            onChange={(e) => setSelectedMonth(Number(e.target.value))}
            className="px-3 py-2 rounded-xl border border-slate-200 bg-slate-50"
          >
            {Array.from({ length: 12 }, (_, i) => i + 1).map((m) => (
              <option key={m} value={m}>
                {new Date(0, m - 1).toLocaleString("en-US", { month: "long" })}
              </option>
            ))}
          </select>
        </div>
      </div>

      {/* Logs Grid */}
      <div className="max-w-7xl mx-auto">
        <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-6">
          {filteredLogs.map((log) => (
            <div
              key={log.id}
              className="group bg-white/90 backdrop-blur-sm rounded-3xl p-6 shadow-sm hover:shadow-xl cursor-pointer transition-all duration-500 border border-white/50 hover:border-white/80"
            >
              {/* Header */}
              <div className="flex justify-between items-start mb-4">
                <div>
                  <h3 className="text-lg font-semibold text-slate-900 mb-1">
                    {formatDate(log.date)}
                  </h3>
                  <div className="flex items-center gap-2 text-sm text-slate-600">
                    <svg
                      className="w-4 h-4"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z"
                      />
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M15 11a3 3 0 11-6 0 3 3 0 016 0z"
                      />
                    </svg>
                    {log.location}
                  </div>
                </div>
                <span
                  className={`px-3 py-1 rounded-full text-xs font-medium ${
                    log.paymentStatus === "Paid"
                      ? "bg-emerald-100 text-emerald-800 border border-emerald-200"
                      : "bg-amber-100 text-amber-800 border border-amber-200"
                  }`}
                >
                  {log.paymentStatus}
                </span>
              </div>

              {/* Products */}
              <div className="mb-4">
                <h4 className="text-sm font-medium text-slate-700 mb-3">
                  PRODUCTS
                </h4>
                <div className="space-y-2">
                  {log.products.map((p, i) => {
                    const totalTons = p.tonnagePerTrip.reduce(
                      (a, b) => a + b,
                      0
                    );
                    return (
                      <div
                        key={i}
                        className="flex justify-between items-center text-sm"
                      >
                        <span className="text-slate-600">{p.name}</span>
                        <div className="text-right">
                          <div className="text-slate-900 font-medium">
                            {totalTons.toFixed(2)}t Ã—{" "}
                            {formatCurrency(PRODUCT_PRICES[p.name])}
                          </div>
                          <div className="text-slate-500 text-xs">
                            {formatCurrency(totalTons * PRODUCT_PRICES[p.name])}
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>

              {/* Transport */}
              <div className="mb-4">
                <h4 className="text-sm font-medium text-slate-700 mb-2">
                  TRANSPORT
                </h4>
                <div className="flex justify-between items-center text-sm">
                  <span className="text-slate-600">{log.trips} trips</span>
                  <div className="text-right">
                    <div className="text-slate-900 font-medium">
                      {formatCurrency(TRANSPORT_COST[log.location])} per trip
                    </div>
                    <div className="text-slate-500 text-xs">
                      {formatCurrency(calculateTransportTotal(log))}
                    </div>
                  </div>
                </div>
              </div>

              {/* Total */}
              <div className="pt-4 border-t border-slate-200/60">
                <div className="flex justify-between items-center">
                  <span className="text-sm font-medium text-slate-700">
                    TOTAL
                  </span>
                  <span className="text-lg font-bold text-slate-900">
                    {formatCurrency(calculateGrandTotal(log))}
                  </span>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Modal for Adding Log */}
      {showModal && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4 animate-in fade-in duration-300">
          <div className="bg-white rounded-3xl p-8 w-full max-w-2xl shadow-2xl border border-white/20 animate-in zoom-in-95 duration-300 max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-slate-900">
                New Log Entry
              </h2>
              <button
                onClick={() => setShowModal(false)}
                className="p-2 hover:bg-slate-100 rounded-2xl transition-colors duration-200"
              >
                <svg
                  className="w-5 h-5 text-slate-500"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M6 18L18 6M6 6l12 12"
                  />
                </svg>
              </button>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {/* Left Column */}
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">
                    Date
                  </label>
                  <input
                    type="date"
                    value={newLog.date}
                    onChange={(e) =>
                      setNewLog({ ...newLog, date: e.target.value })
                    }
                    className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-2xl focus:ring-2 focus:ring-emerald-500 focus:border-emerald-500 outline-none transition-all duration-200"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">
                    Location
                  </label>
                  <select
                    value={newLog.location}
                    onChange={(e) =>
                      setNewLog({
                        ...newLog,
                        location: e.target.value,
                        transportCost: TRANSPORT_COST[e.target.value],
                      })
                    }
                    className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-2xl focus:ring-2 focus:ring-emerald-500 focus:border-emerald-500 outline-none transition-all duration-200"
                  >
                    <option value="Idu">Idu</option>
                    <option value="Aviation">Aviation</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">
                    Payment Status
                  </label>
                  <select
                    value={newLog.paymentStatus}
                    onChange={(e) =>
                      setNewLog({ ...newLog, paymentStatus: e.target.value })
                    }
                    className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-2xl focus:ring-2 focus:ring-emerald-500 focus:border-emerald-500 outline-none transition-all duration-200"
                  >
                    <option>Paid</option>
                    <option>Pending</option>
                  </select>
                </div>
              </div>

              {/* Right Column */}
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-slate-700 mb-2">
                    Transport Details
                  </label>
                  <input
                    type="number"
                    min={1}
                    value={newLog.trips}
                    onChange={(e) => {
                      const trips = Math.max(1, Number(e.target.value));
                      const updatedProducts = newLog.products.map((p) => ({
                        ...p,
                        tonnagePerTrip:
                          p.tonnagePerTrip.length === trips
                            ? p.tonnagePerTrip
                            : Array(trips).fill(0),
                      }));
                      setNewLog({
                        ...newLog,
                        trips,
                        products: updatedProducts,
                      });
                    }}
                    className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-2xl focus:ring-2 focus:ring-emerald-500 focus:border-emerald-500 outline-none transition-all duration-200"
                    placeholder="Number of trips"
                  />
                </div>

                {/* Products */}
                <div>
                  <div className="flex justify-between items-center mb-2">
                    <label className="block text-sm font-medium text-slate-700">
                      Products
                    </label>
                    <button
                      type="button"
                      className="text-sm text-emerald-600 hover:text-emerald-700 font-medium"
                      onClick={() =>
                        setNewLog({
                          ...newLog,
                          products: [
                            ...newLog.products,
                            {
                              name: "Dust",
                              tonnagePerTrip: Array(newLog.trips).fill(0),
                            },
                          ],
                        })
                      }
                    >
                      + Add Product
                    </button>
                  </div>

                  <div className="space-y-3">
                    {newLog.products.map((product, index) => (
                      <div key={index} className="space-y-1">
                        <div className="flex items-center gap-2">
                          <select
                            className="px-3 py-2 bg-slate-50 border border-slate-200 rounded-xl focus:ring-1 focus:ring-emerald-500 focus:border-emerald-500 outline-none text-sm flex-1"
                            value={product.name}
                            onChange={(e) => {
                              const updatedProducts = [...newLog.products];
                              updatedProducts[index].name = e.target.value;
                              setNewLog({
                                ...newLog,
                                products: updatedProducts,
                              });
                            }}
                          >
                            {Object.keys(PRODUCT_PRICES).map((name) => (
                              <option key={name}>{name}</option>
                            ))}
                          </select>
                          <div className="text-sm font-medium text-slate-700">
                            {formatCurrency(PRODUCT_PRICES[product.name])}/t
                          </div>
                        </div>

                        <div className="grid grid-cols-1 gap-2 md:grid-cols-3 mt-1">
                          {product.tonnagePerTrip.map((ton, tripIndex) => (
                            <input
                              key={tripIndex}
                              type="number"
                              min={0}
                              step={0.01}
                              placeholder={`Trip ${tripIndex + 1} tonnage`}
                              className="px-3 py-2 bg-slate-50 border border-slate-200 rounded-xl focus:ring-1 focus:ring-emerald-500 focus:border-emerald-500 outline-none text-sm"
                              value={ton}
                              onChange={(e) => {
                                const updatedProducts = [...newLog.products];
                                updatedProducts[index].tonnagePerTrip[
                                  tripIndex
                                ] = Number(e.target.value);
                                setNewLog({
                                  ...newLog,
                                  products: updatedProducts,
                                });
                              }}
                            />
                          ))}
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>

            {/* Modal footer */}
            <div className="flex justify-end gap-3 mt-8 pt-6 border-t border-slate-200">
              <button
                onClick={() => setShowModal(false)}
                className="px-6 py-3 rounded-2xl border border-slate-300 text-slate-700 hover:bg-slate-50 font-medium transition-colors duration-200"
              >
                Cancel
              </button>
              <button
                onClick={handleAddLog}
                className="px-6 py-3 rounded-2xl bg-gradient-to-r from-emerald-600 to-green-600 text-white font-medium hover:from-emerald-700 hover:to-green-700 shadow-lg hover:shadow-xl transition-all duration-200"
              >
                Create Log Entry
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
